<template>
  <div class="orderMsg">
    <div class="main" v-show="this.role==1001||this.role==2001">
      <div class=" search">
        <el-form :inline="true" @submit.native.prevent class="demo-form-inline">
          <el-form-item>
            <el-input placeholder="" size="small" @keyup.enter.native="searchEnterFun" v-model.trim="poid" clearable></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" icon="el-icon-search" size="mini" @click.native="search()"></el-button>
          </el-form-item>
        </el-form>
      </div>
      <el-table :data="tableData" style="width: 100%">
        <el-table-column prop="productOrder.poid" label="">
        </el-table-column>
        <el-table-column prop="user.name" label="">
        </el-table-column>
        <el-table-column prop="user.telephone" label="">
        </el-table-column>
        <el-table-column prop="product.name" label="">
        </el-table-column>
        <el-table-column prop="productOrder.count" label="">
        </el-table-column>
        <el-table-column prop="product.sumPrice" label="">
        </el-table-column>
        <el-table-column prop="productOrder.createTime" label="" :formatter="timeFormatter">
        </el-table-column>
        <el-table-column prop="productOrder.orderTime" label="" :formatter="orderTimeFormatter">
        </el-table-column>
        <el-table-column prop="productOrder.status" label="">
          <template slot-scope="scope">
            <span v-show="scope.row.productOrder.status==false"></span>
            <span v-show="scope.row.productOrder.status==true"></span>
          </template>
        </el-table-column>
        <!-- <el-table-column prop="productOrder.status" label="">
          <template slot-scope="scope">
            <el-button type="text" icon="el-icon-goods" @click.native="extract(scope.row)" size="mini" v-if="scope.row.productOrder.status==false"></el-button>
            <el-button type="text" icon="el-icon-goods" size="mini" disabled v-show="scope.row.productOrder.status==true"></el-button>
          </template>
        </el-table-column> -->
      </el-table>
      <div class="pagination">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 30, 50, 100]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="count">
        </el-pagination>
      </div>
    </div>
    <div class="main" v-show="this.role==3001">
      <el-table :data="tableList" style="width: 100%">
        <el-table-column prop="productOrder.poid" label="">
        </el-table-column>
        <el-table-column prop="product.name" label="">
        </el-table-column>
        <el-table-column prop="productOrder.count" label="">
        </el-table-column>
        <el-table-column prop="product.sumPrice" label="">
        </el-table-column>
        <el-table-column prop="createTime" label="" :formatter="createTimeFormatter">
        </el-table-column>
        <el-table-column prop="orderTime" label="" :formatter="orderTime">
        </el-table-column>
        <el-table-column prop="productOrder.status" label="">
          <template slot-scope="scope">
            <span v-show="scope.row.productOrder.status==false"></span>
            <span v-show="scope.row.productOrder.status==true"></span>
          </template>
        </el-table-column>
        <el-table-column prop="productOrder.status" label="">
          <template slot-scope="scope">
            <el-button type="text" icon="el-icon-goods" @click.native="extract(scope.row)" size="mini" v-if="scope.row.productOrder.status==false"></el-button>
            <el-button type="text" icon="el-icon-goods" size="mini" disabled v-show="scope.row.productOrder.status==true"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination @size-change="handleSizeChanges" @current-change="handleCurrentChanges" :current-page="curr" :page-sizes="[10, 30, 50, 100]" :page-size="page" layout="total, sizes, prev, pager, next, jumper" :total="counts">
        </el-pagination>
      </div>
    </div>
  </div>
</template>
<script>
import axios from 'axios';
import appConfig from '@/util/appConfig';
const api = appConfig.getDataUrl();
import moment from 'moment';
export default {
	data() {
		return {
			tableData: [],
			tableList: [],
			pageSize: 10,
			page: 10,
			currentPage: 1,
			curr: 1,
			count: 0,
			counts: 0,
			token: '',
			poid: '',
			uid: '',
			role: ''
		};
	},
	mounted() {
		this.role = localStorage.getItem('role');
		this.token = localStorage.getItem('token');
		this.uid = localStorage.getItem('uid');
		this.getData();
	},
	methods: {
		getData() {
			if (this.role == 1001 || this.role == 2001) {
				axios
					.get(
						`${api}/store/productOrder/page?poid=${
							this.poid
						}&index=${this.currentPage}&size=${this.pageSize}`,
						{
							headers: {
								//
								Authorization: this.token
							}
						}
					)
					.then((res) => {
						if (res.data.code == 20000) {
							this.count = res.data.data.count;
							for (
								let i = 0;
								i < res.data.data.list.length;
								i++
							) {
								//
								res.data.data.list[i].product.sumPrice =
									res.data.data.list[i].product.price *
									res.data.data.list[i].productOrder.count;
							}
							this.tableData = res.data.data.list;
						} else if (res.data.code == 40001) {
							this.$message({
								message: ',',
								type: 'warning'
							});
							// localStorage.clear();
							this.$router.push({
								path: '/login'
							});
						}
					});
			}
			if (this.role == 3001) {
				axios
					.get(
						`${api}/store/productOrder/customer?uid=${
							this.uid
						}&index=${this.currentPage}&size=${this.pageSize}`,
						{
							headers: {
								//
								Authorization: this.token
							}
						}
					)
					.then((res) => {
						if (res.data.code == 20000) {
							this.counts = res.data.data.count;
							for (
								let i = 0;
								i < res.data.data.list.length;
								i++
							) {
								//
								res.data.data.list[i].product.sumPrice =
									res.data.data.list[i].product.price *
									res.data.data.list[i].productOrder.count;
							}
							this.tableList = res.data.data.list;
						} else if (res.data.code == 40001) {
							this.$message({
								message: ',',
								type: 'warning'
							});
							// localStorage.clear();
							this.$router.push({
								path: '/login'
							});
						}
					});
			}
		},
		handleCurrentChange(val) {
			this.currentPage = val;
			this.getData();
		},
		handleSizeChange(val) {
			this.pageSize = val;
			this.getData();
		},
		handleCurrentChanges(val) {
			this.curr = val;
			this.getData();
		},
		handleSizeChanges(val) {
			this.page = val;
			this.getData();
		},
		timeFormatter(row) {
			return moment(row.productOrder.createTime).format('YYYY-MM-DD HH:mm:ss');
		},
		orderTimeFormatter(row) {
			return moment(row.productOrder.orderTime).format('YYYY-MM-DD HH:mm:ss');
		},
		createTimeFormatter(val) {
			return moment(val.createTime).format('YYYY-MM-DD HH:mm:ss');
		},
		orderTime(val) {
			return moment(val.orderTime).format('YYYY-MM-DD HH:mm:ss');
		},
		search() {
			this.getData();
		},
		extract(row) {
			console.log(row);

			let params = {};
			params.poid = row.productOrder.poid;
			params.status = true;
			let param = new FormData();
			//data
			//keyid
			for (let key in params) {
				//append()keyFormData
				param.append(key, params[key]);
			}
			axios
				.patch(`${api}/store/productOrder/status`, param, {
					headers: {
						//
						Authorization: this.token
					}
				})
				.then((res) => {
					if (res.data.code == 20000) {
						this.page = 10;
						this.curr = 1;
						this.getData();
						this.$message({
							message: '',
							type: 'success'
						});
					}
				});
		}
	}
};
</script>

<style lang="less">
.orderMsg {
}
</style>
