<template>
  <div class="home">
    <el-container>
      <el-aside width="220px">
        <leftNav />
      </el-aside>
      <el-container>
        <el-header>
          <headerNav />
        </el-header>
        <el-main>
          <router-view v-if="isActive"></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>
<script>
import headerNav from '@/views/Header';
import leftNav from '@/views/leftNav';
export default {
	components: {
		leftNav,
		headerNav
	},
	data() {
		return {
			/* */
			isActive: true
		};
	},
	methods: {
		/* ()*/
		reload() {
			this.isActive = false;
			this.$nextTick(function() {
				/* */
				this.isActive = true;
			});
		}
	}
};
</script>
<style lang="less">
.home {
	.el-main {
		padding: 0;
		padding-left: 10px;
	}
	.el-header {
		padding: 0;
		padding-left: 10px;
		height: 50px !important;
	}
}
</style>
