<template>
    <div class="userMsg">
        <div class="main">
            <div class="search">
                <el-form :inline="true" @submit.native.prevent class="demo-form-inline">
                    <el-form-item>
                        <el-input placeholder="" size="small" v-model.trim="username" clearable></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" icon="el-icon-search" size="mini" @click.native="search()"></el-button>
                    </el-form-item>
                </el-form>
            </div>
            <el-table :data="tableData" style="width: 100%">
                <el-table-column prop="uid" label="">
                </el-table-column>
                <el-table-column prop="name" label="">
                </el-table-column>
                <el-table-column prop="username" label="">
                </el-table-column>
<!--                <el-table-column prop="password" label="">-->
<!--                </el-table-column>-->
                <el-table-column prop="roleType" label="">
                    <template slot-scope="scope">
                        <span v-show="scope.row.roleType==1001"></span>
                        <span v-show="scope.row.roleType==2001"></span>
                        <span v-show="scope.row.roleType==3001"></span>
                    </template>
                </el-table-column>
                <el-table-column prop="telephone" label="">
                </el-table-column>
                <el-table-column prop="createTime" label="" :formatter="timeFormatter">
                </el-table-column>
                <el-table-column prop="" label="">
                    <template slot-scope="scope">
                        <el-button type="text" icon="el-icon-edit" @click.native="editUser(scope.row)" size="mini"></el-button>
                        <el-button type="text" icon="el-icon-delete" @click.native="delUser(scope.row)" size="mini"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 30, 50, 100]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="count" v-show="count>0">
                </el-pagination>
            </div>
            <!--  -->
            <el-dialog width="600px" title="" lable-width="100px" :visible.sync="createView" class='domain_dialog' :close-on-click-modal="false">
                <el-form :model="create" ref="create" label-width="100px" :rules="rules" @submit.native.prevent>
                    <el-form-item label=":" prop="name">
                        <el-input v-model="create.name" placeholder="" clearable disabled></el-input>
                    </el-form-item>
                              <el-form-item label=":" prop="roleType">
                    <el-radio-group v-model="create.roleType">
                        <el-radio :label="1001"></el-radio>
                        <el-radio :label="2001"></el-radio>
                        <el-radio :label="3001"></el-radio>
                    </el-radio-group>
                </el-form-item>
                    <el-form-item label=":" prop="telephone">
                        <el-input v-model="create.telephone" placeholder="" clearable disabled></el-input>
<!--                    </el-form-item>-->
<!--                        <el-form-item label=":" prop="password">-->
<!--                        <el-input v-model="create.password" placeholder="" clearable></el-input>-->
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button type="primary" @click.native="editSave('create')"> </el-button>
                    <el-button @click.native="cancel('create')"> </el-button>
                </div>
            </el-dialog>
        </div>
    </div>
</template>
<script>
    import axios from 'axios';
    import appConfig from '@/util/appConfig';
    const api = appConfig.getDataUrl();
    import moment from 'moment';
    export default {
        data() {
            var checkPhone = (rule, value, callback) => {
                if (!value) {
                    return callback(new Error(''));
                } else {
                    const reg = /^1[3|4|5|7|8][0-9]\d{8}$/;
                    if (reg.test(value)) {
                        callback();
                    } else {
                        return callback(new Error(''));
                    }
                }
            };
            return {
                tableData: [],
                pageSize: 10,
                page: 10,
                currentPage: 1,
                username: '',
                count: 0,
                createView: false,
                create: {
                    name: '',
                    telephone: '',
                    password: '',
                    roleType: ''
                },
                token: '',
                rules:{
                    telephone: [
                        {
                            required: true,
                            message: '',
                            trigger: 'blur'
                        },
                        { validator: checkPhone, trigger: 'blur' }
                    ]
                }
            }
        },
        mounted() {
            this.token = localStorage.getItem('token');
            this.getData();
        },
        methods: {
            getData() {
                axios
                    .get(
                        `${api}/store/user/getByUsername?username=${
                            this.username
                        }&index=${this.currentPage}&size=${this.pageSize}`,
                        {
                            headers: {
                                //
                                Authorization: this.token
                            }
                        }
                    )
                    .then((res) => {
                        if (res.data.code == 20000) {
                            this.count = res.data.data.count;
                            this.tableData = res.data.data.list;
                        } else if (res.data.code == 40001) {
                            this.$message({
                                message: ',',
                                type: 'warning'
                            });
                            // localStorage.clear();
                            this.$router.push({
                                path: '/login'
                            });
                        }
                    });
            },
            handleCurrentChange(val) {
                this.currentPage = val;
                this.getData();
            },
            handleSizeChange(val) {
                this.pageSize = val;
                this.getData();
            },
            search() {
                this.getData();
            },
            timeFormatter(row){
                return moment(row.createTime).format('YYYY-MM-DD HH:mm:ss');
            },
            cancel(formName){
                this.$refs[formName].resetFields();
                this.createView = false;
            },
            editUser(row) {
                this.create = JSON.parse(JSON.stringify(row));
                this.createView = true;
            },
            editSave() {
                let param = new FormData();
                //data
                //keyid
                let dataList = JSON.parse(JSON.stringify(this.create));
                for (let key in dataList) {
                    //append()keyFormData
                    param.append(key, dataList[key]);
                }
                axios
                    .patch(`${api}/store/user/info/admin`, param, {
                        headers: {
                            //
                            Authorization: this.token
                        }
                    })
                    .then((res) => {
                        if (res.data.code == 20000) {
                            this.createView = false;
                            this.currentPage = 1;
                            this.pageSize10;
                            this.getData();
                            this.create = {};
                            this.$message({
                                message: '',
                                type: 'success'
                            });
                        } else {
                            this.$message.error(res.data.msg);
                        }
                    });
            },
            delUser(val) {
                this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                })
                    .then(() => {
                        let param = new FormData();
                        //data
                        //keyid
                        val.isDelete = true;
                        for (let key in val) {
                            //append()keyFormData
                            param.append(key, val[key]);
                        }
                        axios
                            .patch(`${api}/store/user/info/admin`, param, {
                                headers: {
                                    //
                                    Authorization: this.token
                                }
                            })
                            .then((res) => {
                                if (res.data.code == 20000) {
                                    this.currentPage = 1;
                                    this.pageSize = 10;
                                    this.getData();
                                    this.$message({
                                        message: '',
                                        type: 'success'
                                    });
                                } else {
                                    this.$message.error(res.data.msg);
                                }
                            });
                    })
                    .catch(() => {
                        this.$message({
                            type: 'info',
                            message: ''
                        });
                    });
            }
        }
    };
</script>

<style lang="less">
    .userMsg {
        .el-form-item__content {
            text-align: left;
        }
        .el-select {
            display: block;
        }
    }
</style>
